 /* prodcut with offer */

/* /////container-1 //// */
.row1{
    max-width:100%;
    min-height: 20rem;
    font-size: 3.4rem;
    padding: 3.4rem 0 3.4rem 20rem;
    box-shadow: 11px 13px 5px -11px rgba(0,0,0,0.75);
}
.heading-img{
   float: left;
    max-width: 14rem;
    max-height: 16rem;
}
.subheading{
    font-size: 2.2rem;  
}
@media (max-width: 890px){
    .row1{
        padding: 3.4rem 4rem 3.4rem 2rem;
    }
}
@media (max-width: 890px){
    /* .subheading{
        text-align: center;
    } */
    .heading{
        width: 100%;
        font-size: 3rem;
    }
}

.row2{
    display: grid;
    max-width: 100%;
    min-height: 34rem;
    grid-template-columns: repeat(3, minmax(24rem, 1fr));
    justify-content: center;
    font-size: 2.2rem;
    column-gap: 1rem;
    background-color: aquamarine;
    box-shadow: 11px 13px 5px -11px rgba(0,0,0,0.75);
    
}
.img21{
    width:100%;
    display: block;
    object-fit: cover;
   
}
.product-pipeline{
    padding: 1rem 0;
    color: rgb(190, 172, 172);
}
.icon2{
    float: left;
}
.product-quantity{
    margin-left: 0;
    margin-top: 1.3rem;
    padding: 1rem 0;
    color: rgb(156, 134, 134);  
}
.All-detailes{
    text-align: center;
}
.detailes{
    cursor: pointer;
    font-weight: 400;
    font-size: 2.2rem;
    border-bottom: 1px solid rgb(156, 134, 134);
}
.status{
    float: left;
    padding-right: 1rem;
    font-size: 2.8rem;
    color: rgb(109, 82, 82);
}
.status-img{
     margin-top:1rem;
}
.time{
    padding-top: 3rem;
}
.minutes{
    color: red;
}
.bidsPlaced{
    padding-top: 10rem;
    font-size: 2.8rem;
    font-weight: 600;
    color: rgb(109, 82, 82);
}
.bides-color{
    color: rgb(24, 194, 24);
}


 /* ////Bids with offer */


 .bids-Products{
    min-height: 30rem;
    background-color: #B9FFF8;
    box-shadow: 11px 13px 5px -11px rgba(0,0,0,0.75);
     font-size: 2.2rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
    justify-content: center;
    gap: 1rem;
}
.col31{
   
     max-width: 100%;

     
}
.innerrow31{
    padding-left: 2rem;
    display: grid;
    grid-template-columns: minmax(10rem, 24rem) 1fr;
    grid-template-rows: minmax(1fr, 20rem);
}   
.innercol32{
    text-align: center;
   
}

.offer-price{
    color: #E38B29;
    padding: 4rem;
    font-size: 3.3rem;
}
.express-content{
    display: grid;
    grid-template-columns:1fr 18rem 1fr ;
    grid-template-rows: 1fr;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.deliveryText{
    font-size: 2rem;
    padding-top: 2.3rem;
    color: rgb(143, 126, 126);
}
.delivery-time{
   color: rgb(143, 126, 126);
    font-size: 1.3rem;
    position: relative;
    top: -1.5rem;
}
.deliveryText2{
    color: rgb(143, 126, 126);
}
.extra-offer-btn{
    background-color: #7FBCD2;
    box-shadow: 0 0 4rem rgba(0, 0 , 0, 0.15);
    cursor: pointer;
    color:aliceblue;
    font-size: 2.2rem;
    margin-top: 7rem;
    margin-bottom: 3rem;
    border: none;
    padding: .4rem;
    border-radius: .7rem;
}
.buy-btn{
   text-align: center;
   padding: 4rem 0;
}
.product-name{
    padding-left: 2rem;
}
.buy-now{
    background-color: #E38B29;
    box-shadow: 0 0 4rem rgba(0, 0 , 0, 0.15);
    cursor: pointer;
    color:aliceblue;
    font-size: 2.2rem;
    padding: .9rem;
    border: none;
    border-radius: .7rem;
}




/*  //////Alternate Product///// */
.container4{
    min-height: 12rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem; 
    font-size: 2.2rem;
    background-color: #FFF5E4;
    font-size: 1.4rem;
    box-shadow: 11px 13px 5px -11px rgba(0,0,0,0.75);
}
.row4{
    margin-top: 2rem;
    margin-left: 1.8rem;

}
.innerrow41{
   display: grid;
   grid-template-columns: 11rem 1fr;
   grid-template-rows: 1fr;
   
}

.ext-image{
    width: 10rem;
    height: 8rem;
}
.external-img{
    width: 100%;
    display: block;
    object-fit: cover;
    border-radius: 1rem;
    opacity: .7;
}
.col43{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
    justify-content: center;
    align-items:center;
}
.col43{
    
    margin: 1rem 0;
}
.online-price-icon{
   float: left;
   padding-left: 1.8rem; 
}
.alt-online-price{
    text-decoration-line: line-through;
}
.offer-price1{
    font-size: 2.4rem;
    color:#E38B29;
    font-weight: 600;    
}
.oreder-now{
   
    border: none;
    background-color: #E38B29;
    padding: 1rem 0 1rem 0;
    border-radius: .4rem;
    width: 7rem;
    margin-left: 3rem;
    cursor: pointer;
}









@media ( max-width:998px){
    .row2{
        grid-template-columns: repeat(2, minmax(24rem , 1fr));
        grid-template-rows:repeat(2, minmax(1fr, 14rem));
        justify-content:start;
        clip-path: polygon(30% 0%, 100% 1%, 100% 46%, 54% 70%, 70% 100%, 0 100%, 0% 70%, 0 1%);
        
    }

    .bids-Products{
        grid-template-columns: repeat(2, 1fr); 
        grid-template-rows: repeat(2, 1fr); 
        clip-path: polygon(30% 0%, 100% 1%, 100% 46%, 54% 70%, 70% 100%, 0 100%, 0% 70%, 0 1%);
    } 


    /*  //////Alternate Product///// */


.container4{
    grid-template-columns: repeat(2, minmax(20rem , 1fr));
    grid-template-rows: repeat(2, minmax(1fr, 20rem));   
}
.oreder-now{
    padding: .7rem 0 .7rem 0;
    width: 9rem;
}











}
@media ( max-width:600px){
    .row2{
      grid-template-columns: 1fr;
      grid-template-rows: repeat(3, minmax(1fr, 14rem));
      clip-path:none;
    }
    .col22{
        padding-left: 2rem;
        border-top: 1px solid rgb(226, 217, 217);
        
    }
    .col23{
        border-top: 1px solid rgb(224, 213, 213);
     padding: 2rem 0 2rem 2rem;
     
    }
    .bids-Products{
        grid-template-columns: 1fr; 
        grid-template-rows:repeat(3, );
        clip-path:none;
    }

  /*  //////Alternate Product///// */

.container4{
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, minmax(1fr, 7rem));   
}
  }































