.nav-center{
    background-color: antiquewhite;
    text-transform: uppercase;
    
     font-size: 1.8rem;
     max-height: 40rem;
     box-shadow: 0px 4px 5px 0px rgba(0,0,0,0.75);
}
.nav-header{
    padding: 1.7rem;
    width:90%;
   
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.search-box{
    padding: 1rem;
    border: none;

}
.search-btn{
    padding: 1rem;
    border: none;
    border-radius: .4rem;
    background: rgb(214, 219, 144);
}
.list-item{
   
    width: 90%;
    margin: 0 auto;
   
}
.items{
    padding: 1rem;
}

.link-container{
    height: 0;
     overflow: hidden;
     transition: .4s;
 }
 .show-container{
     height: 17rem;
 }
 .toggle-btn{
    padding: 1rem;
    color: black;
    background-color: wheat;
    border: none;
    cursor: pointer;
    
 }
 @media (min-width:750px){
    .toggle-btn{
       display: none;
    }
    .nav-center{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: minmax(2rem, 8rem);
        justify-content: center;
        align-items: center;


    }
    .nav-header{
        align-items: baseline;
    }
    .list-item{
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    .items{
        padding: 0;
    }
    .nav-center{
        min-height: 7rem;
    }
    .link-container{
        height: auto !important;
    }
 }

















